import React from "react";
import PropTypes from "prop-types";
import {
  Card,
  CardHeader,
  Button,
  ListGroup,
  ListGroupItem,
  Progress
} from "shards-react";

const UserDetails = ({ userDetails }) => (
  <Card small className="mb-4 pt-3">
    <CardHeader className="border-bottom text-center">
      <div className="mb-3 mx-auto">
        <img
          className="rounded-circle"
          src={userDetails.image}
          alt={userDetails.name}
          width="250"
        />
      </div>
      <h4 className="mb-0">{userDetails.name}</h4>
    </CardHeader>
    <ListGroup flush>
      <ListGroupItem className="px-4">
        <div className="progress-wrapper">
          <strong className="text-muted d-block mb-2">
            {userDetails.description}
          </strong>
          <Progress
            className="progress-sm"
            value={userDetails.contact}
          >
          </Progress>
        </div>
      </ListGroupItem>
      <ListGroupItem className="p-4">
        <strong className="text-muted d-block mb-2">
          {userDetails.metaTitle}
        </strong>
        <span>{userDetails.metaValue}</span>
      </ListGroupItem>
    </ListGroup>
  </Card>
);

UserDetails.propTypes = {
  userDetails: PropTypes.object
};

UserDetails.defaultProps = {
  userDetails:
  {
    id: "1",
    name: "B Ka",
    description: "T l Dng v c bit n vi rt nhiu ci tn khc: Gt, Candy, B bo, TD hehe Ngh nghip th ti gi t cng cha bit sp xp n vo mc g na :P  Nhng d sao th t c s thch l am m nu n, thng thc  n v c bit l c caf na ^^ Theo t, nu n khng c mt cng thc chun no c v mi ngi s t tm cho mnh cch nu n ring, k ai ging ai. N ph thuc nhiu vo cm nhn ca mi ngi v mn n, vo cc nguyn liu, cng c m bn c cng nh iu kin v khng gian v thi gian na., v tt nhin l c cm xc ca bn khi nu ^^Cook as you feel Hy nu nh bn cm nhn v mn n  nh!",
    image: "https://icdn.dantri.com.vn/thumb_w/640/2018/5/23/net-cuoi-be-gai-9-1527053440039156820618.jpg",
    create: "10/11/2019",
    contact: "0178273644",
    status: "action"
  },
};

export default UserDetails;
