import React from "react";

const AddRecipeDetail = () => (
  <form style={{ background: "white", padding: "10px" }}>
    <div className="form-group">
      <label>Tn cng thc</label>
      <input
        type="text"
        className="form-control"
        placeholder="Nhp vo tn cng thc"
      />
    </div>
    <div className="form-group">
      <label>Tc gi</label>
      <input
        type="text"
        className="form-control"
        placeholder="ngi to cng thc"
      />
    </div>
    <div className="form-group">
      <label>Thi gian to </label>
      <input
        type="text"
        className="form-control"
        placeholder="ngy to cng thc"
      />
    </div>
    <div className="form-group">
      <label>Trng thi</label>
      <input type="text" className="form-control" placeholder="Trng thi " />
    </div>
    <div className="custom-file mb-3">
      <input type="file" className="custom-file-input" id="customFile2" />
      <label className="custom-file-label" htmlFor="customFile2">
        Chn hnh nh
      </label>
    </div>
    <button type="submit" className="btn btn-primary">
      Thm
    </button>
  </form>
);

export default AddRecipeDetail;
