import React from "react";
import PropTypes from "prop-types";
import {
    Card,
    CardHeader,
    ListGroup,
    ListGroupItem,
    Row,
    Col,
    Form,
    FormGroup,
    FormInput,
    FormSelect,
    FormTextarea,
    Button
} from "shards-react";

const DetailClassUI = ({ classDetail }) => (
    <Card style={{ width: '100%', margin: '30px', padding: '20px' }}>
        <CardHeader className="border-bottom">
            <h6 className="m-0">Chi tit lp hc</h6>
        </CardHeader>
        <ListGroup flush>
            <ListGroupItem className="p-3">
                <Row>
                    <Col>
                        <Form>
                            <Row form>
                                <Col md="6" className="form-group">
                                    <label htmlFor="feFirstName">Tn lp hc</label>
                                    <FormInput
                                        id="feFirstName"
                                        placeholder="tn ngi dng"
                                        value={classDetail.title}
                                        onChange={() => { }}
                                    />
                                </Col>
                                <Col md="6" className="form-group">
                                    <label htmlFor="feLastName">Loi lp hc</label>
                                    <FormInput
                                        id="feLastName"
                                        placeholder="Loi lp hc"
                                        value={classDetail.category}
                                        onChange={() => { }}
                                    />
                                </Col>
                            </Row>
                            <Row form>
                                <Col md="6" className="form-group">
                                    <label htmlFor="feFirstName" style={{ marginRight: '20px' }}>Hnh nh lp hc: </label>
                                    <img
                                        src={classDetail.image}
                                        alt={classDetail.title}
                                        width="300"
                                    />
                                </Col>
                            </Row>
                            <FormGroup>
                                <label htmlFor="feAddress">a ch</label>
                                <FormInput
                                    id="feAddress"
                                    placeholder="a ch"
                                    value={classDetail.adress}
                                    onChange={() => { }}
                                />
                            </FormGroup>
                            <Row form>
                                {/* First Name */}
                                <Col md="6" className="form-group">
                                    <label htmlFor="feFirstName">u bp</label>
                                    <FormInput
                                        id="feFirstName"
                                        placeholder="u bp "
                                        value={classDetail.author}
                                        onChange={() => { }}
                                    />
                                </Col>
                                <Col md="6" className="form-group">
                                    <label htmlFor="feFirstName" style={{ marginRight: '20px' }}>Hnh nh u bp: </label>
                                    <img
                                        src={classDetail.authorAvatar}
                                        alt={classDetail.author}
                                        width="180"
                                    />
                                </Col>
                            </Row>
                            <Row form>
                                {/* First Name */}
                                <Col md="6" className="form-group">
                                    <label htmlFor="feFirstName">Ngy hc</label>
                                    <FormInput
                                        id="feFirstName"
                                        placeholder="u bp "
                                        value={classDetail.date}
                                        onChange={() => { }}
                                    />
                                </Col>
                                {/* Last Name */}
                                <Col md="6" className="form-group">
                                    <label htmlFor="feLastName">Trng thi ti khon</label>
                                    <FormInput
                                        id="feLastName"
                                        placeholder="Trng thi ti khon"
                                        value={classDetail.status}
                                        onChange={() => { }}
                                    />
                                </Col>
                            </Row>
                            <Row form>
                                {/* Description */}
                                <Col md="12" className="form-group">
                                    <label htmlFor="feDescription">Miu t lp hc</label>
                                    <FormTextarea id="feDescription" rows="5" value={classDetail.description} />
                                </Col>
                            </Row>
                            <Button theme="accent">Chnh sa</Button>
                        </Form>
                    </Col>
                </Row>
            </ListGroupItem>
        </ListGroup>
    </Card>
);

DetailClassUI.propTypes = {
    /**
     * The component's title.
     */
    classDetail: PropTypes.string
};

DetailClassUI.defaultProps = {
    classDetail:
    {
        image: "https://media.cooky.vn/cookingclass/s/cooky-cookingclass-cover-c290.jpg",
        category: "Lp Hc Lm Mt",
        categoryTheme: "dark",
        author: "Nguyn Th Yn",
        authorAvatar: "https://media.cooky.vn/usr/g16/156892/avt/c100x100/cooky-avatar-636734886921773210.jpg",
        title: "Lp Hc Lm Mt: Mt Bi v Mt Chanh Dy",
        adress: "S 2 L trng Tn, Thanh Xun, H Ni",
        description:
            "n vi lp hc bn s c hc cch lm mt bi thanh thanh thm thm, mt chanh dy chua chua ngt ngt cho mm c ngy Tt. Gi cc k u i ch khi bn NG K NGAY ti Cooky !!",
        date: "28 February 2019",
        status: "action"
    },
};

export default DetailClassUI;
